*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html, body {
  background-color: #fff;
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%; }

body * {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  color: black;
  font-family: "Roboto", sans-serif;
  font-size: 62.5%; }

.button {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  background-color: #0691D2;
  border: 2px solid #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 4px 1px rgba(34, 34, 34, 0.6);
  color: #fff;
  cursor: pointer;
  font-size: 1.6rem;
  letter-spacing: .2rem;
  outline: none;
  padding: 1rem;
  text-transform: uppercase;
  transition: transform .5s; }
  .button:hover {
    transform: scale(1.1); }
  .button--strong {
    background-color: #D60230; }

.letter {
  background-color: #E2DAC0;
  border: 1px solid rgba(34, 34, 34, 0.5);
  border-radius: 5px;
  box-shadow: 1px 1px #E2DAC0, 2px 2px #E2DAC0, 3px 3px #E2DAC0, 4px 4px #E2DAC0, 5px 5px #E2DAC0, 6px 6px #E2DAC0, 7px 7px 4px 1px rgba(34, 34, 34, 0.6);
  color: #005A35;
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-size: 3rem;
  height: 6rem;
  line-height: 6rem;
  margin: 0 1rem 1.5rem 0;
  text-align: center;
  text-shadow: 0 -1px 1px #000;
  text-transform: uppercase;
  width: 6rem; }

.letter-group {
  align-items: flex-end;
  display: flex;
  flex-flow: column nowrap; }

.letters-container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  margin-bottom: 2rem; }

.header {
  align-items: center;
  background-image: linear-gradient(45deg, #005A35, #76AB24, #005A35);
  border-bottom: 2px solid #fff;
  display: flex;
  flex-flow: row wrap;
  grid-area: header;
  justify-content: space-between;
  padding: 2rem; }

.word {
  margin-right: 3rem; }

.footer {
  background-color: #222;
  color: #E2DAC0;
  font-size: 1.5rem;
  grid-area: footer; }

.wrapper {
  display: grid;
  grid-template-areas: "header header" "board scores" "footer footer";
  grid-template-columns: minmax(70%, 80%) minmax(20%, 30%);
  grid-template-rows: 15% auto 5%;
  height: 100%; }

.form-group {
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  position: relative; }
  .form-group input {
    border: 2px solid #76AB24;
    border-radius: 5px;
    margin: 0 1rem;
    padding: 1rem;
    position: relative; }
    .form-group input::before {
      border-radius: inherit;
      background: red;
      content: '';
      position: absolute;
      height: 1rem;
      bottom: 0;
      left: 0;
      width: 100%; }

.board {
  background-color: #76AB24;
  display: flex;
  flex-flow: column nowrap;
  font-size: 1.5rem;
  grid-area: board;
  justify-content: space-between;
  position: relative; }
  .board__panel {
    align-items: flex-start;
    display: flex;
    flex-flow: row nowrap;
    flex: 1;
    justify-content: space-between;
    padding: 1rem 3rem; }
    .board__panel .player-name {
      font-size: 2rem;
      text-transform: uppercase; }
  .board__word {
    flex: 7;
    padding: 3rem; }
  .board__summary {
    align-items: center;
    border-top: 2px solid #fff;
    display: flex;
    flex-flow: row nowrap;
    flex: 1;
    justify-content: space-between;
    padding: 1rem 3rem; }
    .board__summary__score span {
      color: #005A35;
      font-weight: bold;
      text-shadow: 2px -2px 2px rgba(34, 34, 34, 0.5);
      font-size: 4rem; }

.word-bonus {
  margin-left: 2rem; }

.bonus {
  display: inline-block; }
  .bonus__button {
    border: 1px solid #fff;
    border-radius: 5px;
    color: #005A35;
    cursor: pointer;
    font-size: 1.3rem;
    outline: none;
    padding: .5rem;
    transition: transform .5s; }
    .bonus__button--x2 {
      background-color: #9DC9EC; }
    .bonus__button--x3 {
      background-color: #E4B96F; }
    .bonus__button--x3word {
      color: #fff;
      background-color: #D60230; }
    .bonus__button--x2word {
      color: #E2DAC0;
      background-color: #0691D2; }
    .bonus__button:hover {
      transform: rotate(20deg); }

.scores {
  background-color: #E2DAC0;
  border-left: 2px solid #fff;
  font-size: 1.2rem;
  grid-area: scores;
  padding: 1rem; }
  .scores .players {
    width: 100%; }
    .scores .players th, .scores .players td {
      text-align: center; }
    .scores .players tfoot {
      background-color: #005A35;
      color: #fff; }
